
.ui-card .ui-list:before {
  display: none;
}
.ui-card .ui-list:after {
  display: none;
}
.ui-card {
  background: #fff;
  margin: 8px;
  position: relative;
  border-radius: 2px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
}



