
.ui-date-select {
  width: 100%;
  height: 36px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  color: #666;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-date-select:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: #d3d6db;
  display: block;
  z-index: 15;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-2 .ui-date-select:after {
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-3 .ui-date-select:after {
  -webkit-transform: scaleY(0.33);
  transform: scaleY(0.33);
}
.ui-date-select:after {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-perspective: 1000;
  perspective: 1000;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  transition-duration: 200ms;
}
.ui-date-select.focus-state:after,
.ui-date-select.not-empty-state:after,
.focus-state .ui-date-select:after,
.not-empty-state .ui-date-select:after {
  background: #ff5252;
  -webkit-transform: scaleY(2) !important;
  transform: scaleY(2) !important;
}
.ui-date-select > .icon-insert_invitation {
  -webkit-box-flex: 0;
  -webkit-flex-shrink: 0;
  -ms-flex: 0 0 auto;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.ui-date-select-text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 16px;
  height: 36px;
  line-height: 36px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
.ui-date-select-text.placeholder {
  color: #7e848c;
}



