
.ui-checkbox {
  position: relative;
  display: inline-block;
  height: 36px;
  line-height: 36px;
  margin-right: 32px;
}
.ui-checkbox input[type="checkbox"] {
  display: none;
}
.ui-checkbox input[type="checkbox"]:checked + .ui-checkbox-icon,
.ui-checkbox input[type="checkbox"]:checked + .ui-checkbox-label + .ui-checkbox-icon {
  border-color: #ff5252;
  background-color: #ff5252;
}
.ui-checkbox input[type="checkbox"]:checked + .ui-checkbox-icon:after,
.ui-checkbox input[type="checkbox"]:checked + .ui-checkbox-label + .ui-checkbox-icon:after {
  opacity: 1;
}
.ui-checkbox * {
  pointer-events: none;
}
.ui-checkbox.label-left .ui-checkbox-label {
  margin-left: 0;
  margin-right: 8px;
}
.ui-checkbox:last-child {
  margin-right: 0;
}
.ui-checkbox-icon {
  position: relative;
  width: 18px;
  height: 18px;
  border-radius: 2px;
  border-width: 2px;
  border-style: solid;
  border-color: #7e848c;
  background: transparent;
  transition-duration: 300ms;
}
.ui-checkbox-icon:after {
  left: 50%;
  top: 50%;
  margin-left: -3px;
  margin-top: -7px;
  width: 6px;
  height: 13px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  opacity: 0;
  transition: opacity .3s ease;
  transition-delay: .1s;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  box-sizing: border-box;
  position: absolute;
  display: block;
  content: "";
}
.ui-checkbox-label {
  color: #666;
  font-size: 16px;
  margin-left: 8px;
  margin-right: auto;
}
.ui-checkbox-icon,
.ui-checkbox-label {
  display: inline-block;
  vertical-align: middle;
}



