
.ui-item {
  position: relative;
  overflow: hidden;
  padding-left: 16px;
  min-height: 48px;
  color: #7e848c;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.ui-item:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.12);
  display: block;
  z-index: 15;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-2 .ui-item:after {
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-3 .ui-item:after {
  -webkit-transform: scaleY(0.33);
  transform: scaleY(0.33);
}
.ui-item-ripple:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.1);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  opacity: 0;
  pointer-events: none;
  transition-duration: 600ms;
}
.ui-item-ripple.active-state:before,
html:not(.watch-active-state) .ui-item-ripple:active:before {
  opacity: 1;
  transition-duration: 150ms;
}
.ui-item-ripple .ui-ripple-ink {
  color: rgba(0, 0, 0, 0.1);
}
.ui-item-link .ui-item-content {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20viewBox%3D'0%200%2060%20120'%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%3E%3Cpath%20d%3D'm60%2061.5-38.25%2038.25-9.75-9.75%2029.25-28.5-29.25-28.5%209.75-9.75z'%20fill%3D'%23989da3'%2F%3E%3C%2Fsvg%3E");
  background-size: 10px 20px;
  background-repeat: no-repeat;
  background-position: 95% center;
  padding-right: 42px;
}



