
.ui-picker-slot {
  -webkit-box-flex: 1;
  -webkit-flex-shrink: 1;
  -ms-flex: 0 1 auto;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  font-size: 18px;
  overflow: hidden;
  position: relative;
  max-height: 100%;
  text-align: center;
}
.ui-picker-slot.ui-picker-slot-divider {
  color: #000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  line-height: 36px;
}
.ui-picker-slot-wrapper.animate {
  transition-duration: .3s;
  transition-timing-function: ease-out;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.ui-picker-item {
  line-height: 36px;
  padding: 0 10px;
  white-space: nowrap;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #7e848c;
  left: 0;
  top: 0;
  width: 100%;
  box-sizing: border-box;
  transition-duration: .3s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.ui-picker-item.selected {
  color: #666;
  -webkit-transform: translate3d(0, 0, 0) rotateX(0);
          transform: translate3d(0, 0, 0) rotateX(0);
}



