
.ui-button {
  color: #666;
  border-radius: 2px;
  height: 36px;
  line-height: 36px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  text-decoration: none;
  text-align: center;
  margin: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-transform: uppercase;
  min-width: 64px;
  padding: 0 8px;
  position: relative;
  overflow: hidden;
  outline: 0;
  border: none;
  transition-duration: 300ms;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-button:active {
  background: rgba(0, 0, 0, 0.1);
}
.ui-button i.icon + span,
.ui-button i.icon + i.icon {
  margin-left: 8px;
}
.ui-button .icon,
.ui-button .ui-button-text {
  pointer-events: none;
}
.ui-button .icon {
  -webkit-box-flex: 0;
  -webkit-flex-shrink: 0;
  -ms-flex: 0 0 auto;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.ui-button .ui-button-text {
  -webkit-box-flex: 1;
  -webkit-flex-shrink: 1;
  -ms-flex: 0 1 auto;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  word-wrap: break-word;
}
.ui-button .ui-ripple-ink {
  color: rgba(0, 0, 0, 0.1);
}
.ui-button.color-red {
  color: #ff5252;
}
.ui-button.color-green {
  color: #4caf50;
}
.ui-button.color-amber {
  color: #ffc107;
}
.ui-button.color-blue {
  color: #108EE9;
}
.ui-button-fill {
  background-color: #7e848c;
  color: #fff;
}
.ui-button-fill .ui-ripple-ink {
  color: rgba(255, 255, 255, 0.4);
}
.ui-button-fill:active {
  background-color: #6a7077;
}
.ui-button-fill.color-red {
  color: #fff;
  background-color: #ff5252;
}
.ui-button-fill.color-red:active {
  background-color: #ff2929;
}
.ui-button-fill.color-green {
  color: #fff;
  background-color: #4caf50;
}
.ui-button-fill.color-green:active {
  background-color: #409343;
}
.ui-button-fill.color-amber {
  color: #fff;
  background-color: #ffc107;
}
.ui-button-fill.color-amber:active {
  background-color: #dda600;
}
.ui-button-fill.color-blue {
  color: #fff;
  background-color: #108EE9;
}
.ui-button-fill.color-blue:active {
  background-color: #0d77c3;
}
.ui-button-big {
  height: 48px;
  line-height: 48px;
  border-radius: 3px;
}
.ui-button-raised {
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
}
html:not(.watch-active-state) .ui-button-raised:active,
.ui-button-raised.active-state {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}



