
.ui-card-content {
  position: relative;
}
.ui-card-content-inner {
  padding: 16px;
  position: relative;
  color: #7e848c;
}
.ui-card-content-inner > p:first-child {
  margin-top: 0;
}
.ui-card-content-inner > p:last-child {
  margin-bottom: 0;
}
.ui-card-content-inner > .ui-list,
.ui-card-content-inner > .ui-content-block {
  margin: -15px;
  width: auto;
}



