
.ui-action-sheet {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  background: #FFF;
  max-height: 70%;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.ui-action-sheet-group {
  position: relative;
}
.ui-action-sheet-group:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.12);
  display: block;
  z-index: 15;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-2 .ui-action-sheet-group:after {
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-3 .ui-action-sheet-group:after {
  -webkit-transform: scaleY(0.33);
  transform: scaleY(0.33);
}
.ui-action-sheet-group:last-child:after {
  display: none;
}
.ui-action-sheet-button,
.ui-action-sheet-label {
  width: 100%;
  font-weight: normal;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 16px;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ui-action-sheet-label {
  font-size: 14px;
  line-height: 1;
  min-height: 57px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #7e848c;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-action-sheet-label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.12);
  display: block;
  z-index: 15;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-2 .ui-action-sheet-label:before {
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-3 .ui-action-sheet-label:before {
  -webkit-transform: scaleY(0.33);
  transform: scaleY(0.33);
}
.ui-action-sheet-label:first-child:before {
  display: none;
}
.ui-action-sheet-button {
  cursor: pointer;
  height: 48px;
  line-height: 48px;
  font-size: 16px;
  color: #666;
}
.ui-action-sheet-button:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.1);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  opacity: 0;
  pointer-events: none;
  transition-duration: 600ms;
}
.ui-action-sheet-button.active-state:before,
html:not(.watch-active-state) .ui-action-sheet-button:active:before {
  opacity: 1;
  transition-duration: 150ms;
}
.ui-action-sheet-button .ui-ripple-ink {
  color: rgba(0, 0, 0, 0.1);
}
.ui-action-sheet-button.color-red {
  color: #ff5252;
}
.ui-action-sheet-button.color-green {
  color: #4caf50;
}
.ui-action-sheet-button.color-amber {
  color: #ffc107;
}
.ui-action-sheet-button.color-blue {
  color: #108EE9;
}
.actionsheet-enter-active,
.actionsheet-leave-active {
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}
.actionsheet-enter,
.actionsheet-leave-to {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}



