
.ui-alert-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.ui-alert-text {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-alert-content .icon + .ui-alert-text {
  margin-left: 12px;
}
.ui-alert-content.info .icon {
  color: #108EE9;
}
.ui-alert-content.success .icon {
  color: #4caf50;
}
.ui-alert-content.warning .icon {
  color: #ffc107;
}
.ui-alert-content.error .icon {
  color: #ff5252;
}



