
.ui-select {
  width: 100%;
  height: 36px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  color: #666;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-select:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: #d3d6db;
  display: block;
  z-index: 15;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-2 .ui-select:after {
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-3 .ui-select:after {
  -webkit-transform: scaleY(0.33);
  transform: scaleY(0.33);
}
.ui-select:after {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-perspective: 1000;
  perspective: 1000;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  transition-duration: 200ms;
}
.ui-select.focus-state:after,
.ui-select.not-empty-state:after,
.focus-state .ui-select:after,
.not-empty-state .ui-select:after {
  background: #ff5252;
  -webkit-transform: scaleY(2) !important;
  transform: scaleY(2) !important;
}
.ui-select > .icon-arrow_drop_down {
  -webkit-box-flex: 0;
  -webkit-flex-shrink: 0;
  -ms-flex: 0 0 auto;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.ui-select-text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 16px;
  height: 36px;
  line-height: 36px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
.ui-select-text.placeholder {
  color: #7e848c;
}
.ui-select-drop-down {
  position: absolute;
  width: 100%;
  top: 36px;
  left: 0;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  z-index: 20;
  background-color: #FFF;
}
.ui-select-drop-down.up {
  top: auto;
  bottom: 36px;
}
.ui-select-options {
  margin: 0;
  list-style: none;
  padding: 0;
  max-height: 216px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.ui-select-option {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 16px;
  padding: 6px 12px;
  min-height: 36px;
  color: #666;
  position: relative;
}
.ui-select-option .ui-ripple-ink {
  color: rgba(0, 0, 0, 0.1);
}
.ui-select-option.selected {
  color: #ff5252;
}
.ui-select-option.selected > .icon {
  display: inline-block;
  vertical-align: middle;
}
.ui-select-option > .icon {
  display: none;
}
.ui-select-option-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}



