
.ui-date-picker {
  display: inline-block;
  background: #666;
  overflow: hidden;
  font-size: 16px;
  font-family: 'Roboto';
  font-weight: 400;
  position: fixed;
  display: block;
  max-width: 400px;
  width: 90%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
}
.ui-picker-box {
  background: #fff;
  width: 100%;
  max-width: 400px;
  height: 280px;
}
.ui-day-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-day {
  width: 14.2857143%;
  text-align: center;
  cursor: pointer;
  height: 40px;
  padding: 0;
  color: #000;
  background: #fff;
  -webkit-box-flex: 10;
  -webkit-flex-shrink: 10;
  -ms-flex: 0 10 auto;
  -ms-flex-negative: 10;
      flex-shrink: 10;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-day > span {
  height: 36px;
  line-height: 36px;
  display: block;
  width: 36px;
}
.ui-day.checked span {
  background: #ff5252;
  color: #FFF !important;
  border-radius: 50%;
}
.ui-week {
  height: 40px;
  width: 100%;
}
.ui-week ul {
  margin: 0;
  padding: 0;
  list-style: none;
  height: 40px;
}
.ui-week ul li {
  width: 14.2%;
  height: 40px;
  line-height: 40px;
  display: inline-block;
  text-align: center;
  background: transparent;
  color: #000;
  font-weight: bold;
}
.ui-passive-day {
  color: #bbb;
}
.unavailable {
  color: #ccc;
  cursor: not-allowed;
}
.ui-date-monthly {
  height: 100px;
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-date-monthly > div {
  display: block;
  text-align: center;
  cursor: pointer;
}
.ui-date-previous,
.ui-date-next {
  position: relative;
  width: 20% !important;
  text-indent: -300px;
  overflow: hidden;
  height: 100px;
  color: #fff;
}
.ui-date-caption {
  width: 60%;
  box-sizing: border-box;
  font-size: 24px;
}
.ui-date-caption a.date-year,
.ui-date-caption a.date-month {
  display: block;
  color: #FFF;
  position: relative;
}
.ui-date-caption a.date-year:before,
.ui-date-caption a.date-month:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(255, 255, 255, 0.15);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  opacity: 0;
  pointer-events: none;
  transition-duration: 600ms;
}
.ui-date-caption a.date-year.active-state:before,
.ui-date-caption a.date-month.active-state:before,
html:not(.watch-active-state) .ui-date-caption a.date-year:active:before,
html:not(.watch-active-state) .ui-date-caption a.date-month:active:before {
  opacity: 1;
  transition-duration: 150ms;
}
.ui-date-caption span:hover {
  color: rgba(255, 255, 255, 0.7);
}
.ui-date-next::before,
.ui-date-previous::before {
  width: 20px;
  height: 2px;
  text-align: center;
  position: absolute;
  background: #fff;
  top: 50%;
  margin-top: -7px;
  margin-left: -7px;
  left: 50%;
  line-height: 0;
  content: '';
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.ui-date-next::after,
.ui-date-previous::after {
  width: 20px;
  height: 2px;
  text-align: center;
  position: absolute;
  background: #fff;
  margin-top: 6px;
  margin-left: -7px;
  top: 50%;
  left: 50%;
  line-height: 0;
  content: '';
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.ui-date-previous::after {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.ui-date-previous::before {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.ui-date-item {
  text-align: center;
  font-size: 20px;
  padding: 10px 0;
  cursor: pointer;
}
.ui-date-item:hover {
  background: #e0e0e0;
}
.ui-date-list {
  overflow: auto;
  vertical-align: top;
  padding: 0;
}
.ui-vue-date {
  display: inline-block;
  color: #5D5D5D;
}
.ui-button-box {
  background: #fff;
  vertical-align: top;
  height: 48px;
  line-height: 48px;
  text-align: right;
  padding: 6px 8px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
.ui-watch-box {
  height: 100%;
  overflow: hidden;
}
.ui-hour-box,
.ui-min-box {
  display: inline-block;
  width: 50%;
  text-align: center;
  height: 100%;
  overflow: auto;
  float: left;
}
.ui-hour-box ul,
.ui-min-box ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.ui-hour-item,
.ui-min-item {
  padding: 10px;
  font-size: 20px;
  cursor: pointer;
}
.ui-hour-box .active,
.ui-min-box .active {
  background: #ff5252;
  color: #FFF !important;
}
::-webkit-scrollbar {
  width: 2px;
}
::-webkit-scrollbar-track {
  background: #E3E3E3;
}
::-webkit-scrollbar-thumb {
  background: #C1C1C1;
  border-radius: 2px;
}



