
.ui-switch {
  position: relative;
  display: inline-block;
  height: 36px;
  line-height: 36px;
  margin-right: 32px;
  color: #7e848c;
}
.ui-switch input[type="checkbox"] {
  display: none;
}
.ui-switch input[type="checkbox"]:checked + .ui-switch-checkbox,
.ui-switch input[type="checkbox"]:checked + .ui-switch-label + .ui-switch-checkbox {
  background: rgba(255, 82, 82, 0.5);
}
.ui-switch input[type="checkbox"]:checked + .ui-switch-checkbox:after,
.ui-switch input[type="checkbox"]:checked + .ui-switch-label + .ui-switch-checkbox:after {
  -webkit-transform: translate3d(16px, 0, 0);
          transform: translate3d(16px, 0, 0);
  background: #ff5252;
}
.ui-switch * {
  pointer-events: none;
}
.ui-switch.label-left .ui-switch-label {
  margin-left: 0;
  margin-right: 8px;
}
.ui-switch:last-child {
  margin-right: 0;
}
.ui-switch-label {
  font-size: 16px;
  color: #666;
  margin-left: 8px;
  margin-right: auto;
}
.ui-switch-checkbox {
  width: 36px;
  border-radius: 36px;
  height: 14px;
  background: #b0afaf;
  z-index: 0;
  margin: 0;
  padding: 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  cursor: pointer;
  position: relative;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  transition: all 300ms;
}
.ui-switch-checkbox:after {
  content: ' ';
  height: 20px;
  width: 20px;
  border-radius: 20px;
  background: #fff;
  position: absolute;
  z-index: 2;
  top: -3px;
  left: 0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.ui-switch-label,
.ui-switch-checkbox {
  display: inline-block;
  vertical-align: middle;
}



