
.ui-toast {
  position: fixed;
  left: 50%;
  bottom: 50px;
  max-width: 80%;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  text-align: center;
  padding: 10px;
  z-index: 1000;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
}
.ui-toast .ui-toast-text {
  font-size: 1.4rem;
  display: block;
  text-align: center;
}
.ui-toast.ui-toast-center {
  top: 50%;
  bottom: auto;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
}
.ui-toast-enter-active,
.ui-toast-leave-active {
  transition: opacity .3s linear;
}
.ui-toast-enter,
.ui-toast-leave-to {
  opacity: 0;
}



