
.ui-radio {
  position: relative;
  display: inline-block;
  height: 36px;
  line-height: 36px;
  margin-right: 32px;
}
.ui-radio input[type="radio"] {
  display: none;
}
.ui-radio input[type="radio"]:checked + .ui-radio-icon,
.ui-radio input[type="radio"]:checked + .ui-radio-label + .ui-radio-icon {
  border-color: #ff5252;
}
.ui-radio input[type="radio"]:checked + .ui-radio-icon:after,
.ui-radio input[type="radio"]:checked + .ui-radio-label + .ui-radio-icon:after {
  -webkit-transform: scale(1);
          transform: scale(1);
}
.ui-radio * {
  pointer-events: none;
}
.ui-radio.label-left .ui-radio-label {
  margin-left: 0;
  margin-right: 8px;
}
.ui-radio:last-child {
  margin-right: 0;
}
.ui-radio-icon {
  width: 20px;
  height: 20px;
  border-radius: 36px;
  border-width: 2px;
  border-style: solid;
  border-color: #7e848c;
  background: transparent;
  transition-duration: 300ms;
  position: relative;
}
.ui-radio-icon:after {
  content: ' ';
  display: block;
  width: 10px;
  height: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -5px;
  margin-top: -5px;
  background-color: #ff5252;
  border-radius: 100%;
  -webkit-transform: scale(0);
          transform: scale(0);
  transition-duration: 300ms;
}
.ui-radio-label {
  color: #666;
  font-size: 16px;
  margin-left: 8px;
  margin-right: auto;
}
.ui-radio-label,
.ui-radio-icon {
  display: inline-block;
  vertical-align: middle;
}



