
.ui-form-list {
  margin: 16px 0;
  width: 100%;
  background: #FFF;
  position: relative;
}
.ui-form-list:first-child {
  margin-top: 0;
}
.ui-form-list:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.12);
  display: block;
  z-index: 15;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-2 .ui-form-list:before {
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-3 .ui-form-list:before {
  -webkit-transform: scaleY(0.33);
  transform: scaleY(0.33);
}
.ui-content-title + .ui-form-list {
  margin-top: 0px;
}



