
.ui-bottom-nav-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
  height: 100%;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: inherit;
}
.ui-bottom-nav-item:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(255, 255, 255, 0.15);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  opacity: 0;
  pointer-events: none;
  transition-duration: 600ms;
}
.ui-bottom-nav-item.active-state:before,
html:not(.watch-active-state) .ui-bottom-nav-item:active:before {
  opacity: 1;
  transition-duration: 150ms;
}
.ui-bottom-nav-item .ui-ripple-ink {
  color: rgba(0, 0, 0, 0.1);
}
.ui-bottom-nav-item.active {
  color: #108EE9;
}
.ui-bottom-nav-item.active .ui-bottom-nav-item-text {
  font-size: 14px;
}
.ui-bottom-nav-item .ui-bottom-nav-item-text {
  font-size: 12px;
  transition: color 0.3s, font-size 0.3s;
}
.ui-bottom-nav-item .icon {
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
}



