
.ui-content-block {
  margin: 16px 0;
  padding: 10px 16px;
  width: 100%;
  color: #7e848c;
}
.ui-content-block p {
  margin-top: 1em;
  margin-bottom: 1em;
}
.ui-content-block p:first-child {
  margin-top: 0;
}
.ui-content-block p:last-child {
  margin-bottom: 0;
}



