
.ui-overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #000;
  opacity: .4;
  z-index: 1000;
}
.ui-overlay-fade-enter-active, .ui-overlay-fade-leave-active {
  transition: opacity .45s cubic-bezier(0.23, 1, 0.32, 1);
}
.ui-overlay-fade-enter,
.ui-overlay-fade-leave-active {
  opacity: 0 !important;
}



