
.ui-prompt-content {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.ui-prompt-text-field {
  width: 100%;
  display: block;
  position: relative;
}
.ui-prompt-text-field:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: auto;
  top: auto;
  height: 1px;
  width: 100%;
  background-color: #d3d6db;
  display: block;
  z-index: 15;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-2 .ui-prompt-text-field:after {
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
html.pixel-ratio-3 .ui-prompt-text-field:after {
  -webkit-transform: scaleY(0.33);
  transform: scaleY(0.33);
}
.ui-prompt-text-field:after {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-perspective: 1000;
  perspective: 1000;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  transition-duration: 200ms;
}
.ui-prompt-text-field.focus-state:after,
.ui-prompt-text-field.not-empty-state:after {
  background: #ff5252;
  -webkit-transform: scaleY(2) !important;
          transform: scaleY(2) !important;
}
.ui-prompt-input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  box-sizing: border-box;
  border: none;
  background: none;
  border-radius: 0 0 0 0;
  box-shadow: none;
  display: block;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 36px;
  color: #666;
  font-size: 16px;
  font-family: inherit;
}



