
.ui-modal {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  outline: none;
  width: 280px;
  margin: 0px auto;
  padding: 0;
  background-color: white;
  border-radius: 3px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
  max-height: 100vh;
  max-width: 100vw;
  overflow-x: hidden;
  overflow-y: auto;
  font-size: 16px;
  color: #7e848c;
}
.ui-modal-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 24px 24px 20px;
}
.ui-modal-header .ui-modal-title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0;
  font-weight: normal;
  color: #666;
}
.ui-modal-header + .ui-modal-body {
  padding-top: 0;
}
.ui-modal-body {
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 24px;
  padding-bottom: 20px;
}
.ui-modal-footer {
  padding: 6px 8px;
  min-height: 48px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
.ui-modal-footer .ui-button-fill,
.ui-modal-footer .ui-button-raise {
  margin-left: 10px;
}
.ui-modal-overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #000;
  opacity: .4;
  z-index: 1000;
}
.ui-modal-fade-enter-active,
.ui-modal-fade-leave-active {
  transition: all .3s linear;
}
.ui-modal-fade-enter,
.ui-modal-fade-leave-to {
  opacity: 0;
}



