
.ui-popover {
  position: fixed;
  background: #FFF;
  max-width: 300px;
  min-width: 200px;
  border-radius: 3px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
}
.ui-popover.ui-popover-bottom {
  -webkit-transform-origin: center top;
          transform-origin: center top;
}
.ui-popover.ui-popover-top {
  -webkit-transform-origin: center bottom;
          transform-origin: center bottom;
}
.ui-popover-content {
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  max-height: 70%;
  border-radius: 3px;
}
.ui-popover-enter-active,
.ui-popover-leave-active {
  transition-duration: 300ms;
  transition-property: opacity, -webkit-transform;
  transition-property: opacity, transform;
  transition-property: opacity, transform, -webkit-transform;
}
.ui-popover-enter,
.ui-popover-leave-to {
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
}
.ui-overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #000;
  opacity: .4;
  z-index: 1000;
}
.ui-overlay-fade-enter-active,
.ui-overlay-fade-leave-active {
  transition: all .3s linear;
}
.ui-overlay-fade-enter,
.ui-overlay-fade-leave-to {
  opacity: 0;
}



