
.ui-overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #000;
  opacity: .4;
  z-index: 1000;
}
.ui-overlay-fade-enter-active,
.ui-overlay-fade-leave-active {
  transition: all .3s linear;
}
.ui-overlay-fade-enter,
.ui-overlay-fade-leave-to {
  opacity: 0 !important;
}



