
.ui-badge {
  font-size: 10px;
  display: inline-block;
  font-style: normal;
  color: #fff;
  background-color: #bbbfc4;
  padding: 3px 6px;
  border-radius: 3px;
  line-height: normal;
}
.icon .ui-badge {
  position: absolute;
  left: 100%;
  margin-left: -10px;
  padding: 1px 6px;
  top: -2px;
  line-height: normal;
  border-radius: 10px;
}
.ui-badge-long {
  background-color: #7e848c;
}
.ui-badge-red {
  background-color: #ff5252;
}
.ui-badge-green {
  background-color: #4caf50;
}
.ui-badge-amber {
  background-color: #ffc107;
}
.ui-badge-blue {
  background-color: #108EE9;
}



