
.ui-item-media {
  padding-top: 10px;
  padding-bottom: 10px;
}
.ui-item-media + .ui-item-content {
  padding-left: 16px;
}
.ui-item-media img {
  max-width: 40px;
  border-radius: 50%;
}
.ui-item-cell .ui-item-media {
  padding-top: 0;
  padding-bottom: 0;
}
.ui-item-cell .ui-item-media + .ui-item-title {
  padding-left: 16px;
}



