
.ui-nav-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 48px;
  padding: 10px 16px;
  position: relative;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #7e848c;
}
.ui-nav-menu:active {
  color: #666;
}
.ui-nav-menu:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.1);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  opacity: 0;
  pointer-events: none;
  transition-duration: 600ms;
}
.ui-nav-menu.active-state:before,
html:not(.watch-active-state) .ui-nav-menu:active:before {
  opacity: 1;
  transition-duration: 150ms;
}
.ui-nav-menu-icon {
  width: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #666;
}
.ui-nav-menu-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.ui-nav-menu-title {
  font-size: 16px;
  -webkit-box-flex: 10;
  -webkit-flex-shrink: 10;
  -ms-flex: 0 10 auto;
  -ms-flex-negative: 10;
      flex-shrink: 10;
}
.ui-nav-menu-after {
  margin-left: auto;
  font-size: 14px;
}



