
.ui-float-button {
  position: relative;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  line-height: 1;
  width: 56px;
  height: 56px;
  background-color: #666;
  border: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  text-decoration: none;
  margin: 0;
  outline: 0;
  transition-duration: 300ms;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  border-radius: 50%;
  color: #FFF;
  -webkit-box-flex: 0;
  -webkit-flex-shrink: 0;
  -ms-flex: 0 0 auto;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}
.ui-float-button:active {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
.ui-float-button:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(255, 255, 255, 0.15);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  opacity: 0;
  pointer-events: none;
  transition-duration: 600ms;
}
.ui-float-button.active-state:before,
html:not(.watch-active-state) .ui-float-button:active:before {
  opacity: 1;
  transition-duration: 150ms;
}
.ui-float-button-fixed {
  position: fixed;
  right: 16px;
  bottom: 16px;
}
.ui-float-button-long {
  background-color: #666;
}
.ui-float-button-red {
  background-color: #ff5252;
}
.ui-float-button-green {
  background-color: #4caf50;
}
.ui-float-button-blue {
  background-color: #108EE9;
}
.ui-float-button-amber {
  background-color: #ffc107;
}



