
.ui-content-title {
  position: relative;
  overflow: hidden;
  text-transform: capitalize;
  margin: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 16px;
  margin: 16px 16px 16px;
  padding-top: 16px;
  font-weight: 500;
}
.ui-content-title + .ui-list,
.ui-content-title + .ui-content-block,
.ui-content-title + .ui-card {
  margin-top: 0px;
}



